<template>
    <div class="main-page">
        <List ref="list" :apis="apis" :render="render" :fieldToPage="fieldToPage" FormCode="oa_goods" :searchInfo="searchInfo">
            <el-button slot="contentRight" type="primary" @click="$refs.FormAE.start(false)">新增</el-button>
        </List>
        <FormAE ref="FormAE" :apis="apis" @refresh="$refs.list.getList()" FormsCode="oa_goods" :initAfterFun="initAfterFun" />
        <FormDetails ref="FormDetails" :apis="apis" FormsCode="oa_goods" />
    </div>
</template>

<script>
export default {
    name: "GoodsList",
    props: {},
    components: {
        List: () => import("@/universalPage/list/List.vue"),
        FormAE: () => import("@/universalPage/formAE/FormAE.vue"),
        FormDetails: () => import("@/universalPage/formDetails/FormDetails.vue")
    },
    data() {
        return {
            apis: {
                edit: this._api.OSMS_MaterialEdit,
                create: this._api.OSMS_MaterialCreate,
                list: this._api.OSMS_MaterialManage,
                delete: this._api.OSMS_MaterialDelete,
                audit: this._api.OSMS_MaterialAudit,
                details: this._api.OSMS_MaterialDetails
            },
            fieldToPage: {
                FName: this.toDetails,
                FNumber:this.toDetails
            },
            searchInfo: {
                FGroupNo: 3
            }
        }
    },
    methods: {
        render(h) {
            return (
                <div>
                    <el-table-column prop="FNumber" label="商品编码" width="" align="left" header-align="center"></el-table-column>
                    <el-table-column prop="FName" label="商品名称" width="" align="left" align="center"></el-table-column>
                    <el-table-column prop="FAbc" label="ABC分类" width="100" align="left" align="center"></el-table-column>
                    <el-table-column prop="Model" label="卷烟规格" width="100" align="center"></el-table-column>
                    <el-table-column prop="FIsSpecial" label="是否异形" width="100" align="center" formatter={(row) => {
                        return (
                            <span>{row.FIsSpecial ? "是" : "否"}</span>
                        )
                    }}></el-table-column>
                    <el-table-column prop="FBarcode" label="卷烟条码" width="160" align="center"></el-table-column>
                    <el-table-column prop="CreateName" label="创建人" width="100" align="center"></el-table-column>
                    <el-table-column prop="CreateTime" label="创建时间" width="160" align="center"></el-table-column>
                </div >
            )
        },
        toDetails(row) {
            this.$refs.FormDetails.start(row)
        },
        initAfterFun(row, info) {
            info.FGroupNo = 3
        }
    },
    mounted() { }
}
</script>

<style lang="scss" scoped>
</style>